/* src/components/AdminPanel.css */
.admin-login {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #fff5f5 0%, #fff 50%, #fff5f5 100%);
}

.login-form {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(245, 98, 122, 0.15);
  text-align: center;
  max-width: 400px;
  width: 90%;
}

.login-form h2 {
  color: #f5627a;
  margin-bottom: 10px;
  font-family: "Great Vibes", cursive;
  font-size: 32px;
}

.login-form p {
  color: #666;
  margin-bottom: 30px;
}

.password-input {
  width: 100%;
  padding: 15px;
  border: 2px solid rgba(245, 98, 122, 0.2);
  border-radius: 10px;
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center;
}

.password-input:focus {
  outline: none;
  border-color: #f5627a;
}

.login-btn {
  width: 100%;
  padding: 15px;
  background: linear-gradient(135deg, #f5627a, #ff9baa);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(245, 98, 122, 0.3);
}

.admin-panel {
  min-height: 100vh;
  padding: 20px;
  background: #f8f9fa;
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid rgba(245, 98, 122, 0.1);
}

.admin-header h1 {
  color: #f5627a;
  font-family: "Great Vibes", cursive;
  font-size: 36px;
  margin: 0;
}

.logout-btn {
  padding: 10px 20px;
  background: #6c757d;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
}

.logout-btn:hover {
  background: #5a6268;
}

.stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: white;
  padding: 25px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  border-top: 4px solid;
}

.stat-card.total {
  border-color: #f5627a;
}

.stat-card.attending {
  border-color: #4caf50;
}

.stat-card.not-attending {
  border-color: #f44336;
}

.stat-card.maybe {
  border-color: #ff9800;
}

.stat-card h3 {
  margin: 0 0 10px 0;
  color: #666;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stat-number {
  font-size: 32px;
  font-weight: bold;
  color: #333;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.refresh-btn {
  padding: 10px 20px;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
}

.refresh-btn:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

.refresh-btn:hover:not(:disabled) {
  background: #218838;
}

.last-updated {
  color: #666;
  font-size: 14px;
}

.loading, .no-data {
  text-align: center;
  padding: 40px;
  color: #666;
  font-size: 18px;
}

.confirmations-list {
  display: grid;
  gap: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.confirmation-card {
  background: white;
  border-radius: 15px;
  padding: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  border-left: 4px solid #f5627a;
}

.card-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

.guest-number {
  background: #f5627a;
  color: white;
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
}

.guest-name {
  margin: 0;
  color: #333;
  flex: 1;
}

.attendance-badge {
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
}

.card-body {
  margin-top: 15px;
}

.message-section {
  margin-bottom: 15px;
}

.message-section strong {
  color: #666;
  font-size: 14px;
}

.message-text {
  margin: 5px 0 0 0;
  color: #333;
  font-style: italic;
  line-height: 1.4;
}

.meta-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  border-top: 1px solid #eee;
  font-size: 12px;
  color: #999;
}

@media (max-width: 768px) {
  .admin-panel {
    padding: 15px;
  }
  
  .admin-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .stats-container {
    grid-template-columns: 1fr 1fr;
  }
  
  .controls {
    flex-direction: column;
    gap: 15px;
    align-items: stretch;
  }
  
  .card-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .meta-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
}